  methods: {
    onSubmit() {
       console.log(this.userId);
      console.log(this.is_initial);
      if (this.validate()) {
        this.$nextTick(() => {
          // submit
          this.formData = JSON.parse(this.formStr);
          var data = {
            'uname':this.formData['userName'],
            'email':this.formData['email'],
            'pword':this.formData['password'],
            'ref_userid':localStorage.user_id
          }

          axios.post(wprestapiPrefix+'/wp-json/custom-plugin/fn_do_signup_MVEN2',data).then(response => {
            console.log('response==>'+response.data['status']);
            if(response.data['status'] == 'success')
            {

              console.log(this.is_initial);
              let asksUri = restapiPrefix+'/caregivers/';
              axios.post(asksUri, {
                first_name: this.formData['firstName'],
                last_name: this.formData['lastName'],
                user_name: this.formData['userName'],
                password: this.formData['password'],
                phone_number: this.formData['phone'],//this.phoneNumber,
                email_address: this.formData['email'],
                hire_date: this.hireDate,
                is_initial: this.is_initial,
                job_role: this.selJobRole,
                caregiver_group: this.selCaregiverGroup,
                is_activated: 1,
                user_id:response.data['user_id'],
                agency_id: this.userId
              }) 
              .then(response => {
                miniToastr.success('Caregiver added successfully', 'Success');
                this.$emit('getNotificationCount', this.userId);
                this.$emit('closeNewCaregiverModalDialog');
                //location.reload();
              })
              .catch(function (error) {
              });
            }
            else{
              miniToastr.error(response.data['message'], 'Error');
            }
          }).then(response => {
              //this.checkAssignedCourses();
          });
          })
      }
     
    },
    /*checkAssignedCourses(){
      var classList = [];
      let asksUri_2 = restapiPrefix+'/caregiverCurriculum/getAllCurriculumsByUserIdAndInitial/'+this.userId+'/1';//this.is_initial;
      this.axios.get(asksUri_2).then(response => {
        if(response && response.data.length>0)
        {
          for(let i = 0; i < response.data.length; i++) {
            classList[i]=response.data[i]['class_id'];
          }
          console.log('classList==>'+classList);
          this.assignCourses(classList);
        }
        else{
          let asksUri_3 = restapiPrefix+'/caregiversClass/getDefaultCourses';
          this.axios.get(asksUri_3).then(response => {
            for(let i = 0; i < response.data.length; i++) {
              classList[i]=response.data[i]['class_id'];
            }
            console.log('classList==>'+classList);
            this.assignCourses(classList);
          });
        }
      });
    },*/
    checkDefaultGroup()
    {
      let asksUri = restapiPrefix+'/caregiverGroups/getGroupsByUserID/'+this.userId;
      this.axios.get(asksUri).then(response => {
        if(response && response.data.length>0)
        {
          this.caregiverGroups = response.data;
        }
        else{
          this.addNewGroup();
        }
      });
    },
    getJobRoleDropdownValue(item) {
      var option = { value: "", text: ""};
      option.value = item.id;
      option.text = item.jobRoleName;
      // console.log(option)
      return option;
    },
    getCaregiverGroupDropdownValue(item) {
      var option = { value: "", text: ""};
      option.value = item.id;
      option.text = item.group_name;
      // console.log(option)
      return option;
    },
    /*assignCourses(classList)
    {
       var date2 = new Date().toJSON().slice(0,10).replace(/-/g,'/')
        let asksUri_1 = restapiPrefix+'/caregiverCurriculum/updateByUserIdAndInitial/'+this.userId+'/1';//+localStorage.is_initial;
        axios.put(asksUri_1, {
          classIdList: classList,
          assigned_date: date2,
          assigned_by: this.assignedBy
        })
        .then(response => {

          var data = {
            'user_id':this.userId,
            'classIdList':classList
          }
          
          //axios.post('http://localhost:8100/onlinecourses_wp/wp-json/custom-plugin/fn_assign_courses_MVEN',data).then(response => {
          axios.post(wprestapiPrefix+'/wp-json/custom-plugin/fn_assign_courses_MVEN',data).then(response => {
            console.log('response==>'+response.data['status']);
            if(response.data['status'] == 'success')
            {
            }
          });

        })
        .catch(function (error) {
          console.log(error);
        });
    },*/
    addNewGroup() {
      // console.log("sdfsd")
        let asksUri = restapiPrefix+'/caregiverGroups/';
        axios.post(asksUri, {
          group_name: 'Default Group',
          is_default: 1,
          user_id: this.userId
        }) 
        .then(response => {
          //this.getGroupListByUserId(this.userId);
          this.checkDefaultGroup();
        })
        .catch(function (error) {
        });
        
      },
    closeAddNewCaregiver() {
      this.$emit('closeNewCaregiverModalDialog')
    },
    onReset() {
      // Reset validation
      this.form = Object.assign({}, formShape)
      this.submitted = false
      this.$nextTick(() => {
        this.$v.$reset()
        this.feedBack = 'secondary'
      })
    },
    chkState(val) {
      const field = this.$v.form[val]
      return !field.$dirty || !field.$invalid
    },
    findFirstError(component = this) {
      if (component.state === false) {
        if (component.$refs.input) {
          component.$refs.input.focus()
          return true
        }
        if (component.$refs.check) {
          component.$refs.check.focus()
          return true
        }
      }
      let focused = false
      component.$children.some((child) => {
        focused = this.findFirstError(child)
        return focused
      })

      return focused
    },
    validate() {
      this.$v.$touch()
      this.$nextTick(() => this.findFirstError())
      return this.isValid
    },
    autoCorrectedDatePipe: () => { return createAutoCorrectedDatePipe('yyyy/mm/dd') }
    
  }
}
</script>

